<template>
    <div class="guess-page">
        <!-- 头 -->
		<van-nav-bar title="实时猜涨跌" fixed left-arrow :border="false" @click-left="goBack" right-text="历史" @click-right="goGuessList"/>

        <!-- 底部按钮栏目 -->
		<div class="bottom">
			<van-row>
				<!-- <van-col class="guess-have" span="6">
					<div @click="showGuessingOrderModalFun">持仓</div>
				</van-col> -->
				<van-col class="guess-up" span="12">
					<div class="guess-up-box">
						<div class="guess-up-box-shadow" v-if="guess_data.is_open == 0"></div>
						<div @click="showGuessModalFun(1)">看涨</div>
					</div>
				</van-col>
				<van-col class="guess-down" span="12" >
					<div class="guess-down-box">
						<div class="guess-down-box-shadow" v-if="guess_data.is_open == 0"></div>
						<div @click="showGuessModalFun(2)">看跌</div>
					</div>
				</van-col>
			</van-row>	
		</div>

        <!-- 模态框 -->
        <div class="modal-box" v-if="showGuessModal"></div>

    </div>
</template>


<script>

import echartsDemo from '../components/EchartsDemo.vue'

import { mapState } from 'vuex'

export default {
    name: "guess",
    components:{
		echartsDemo	
	},
    data(){
        return{
            isLogin:false,
            guess_money_index:0,
			guess_count_index:0,
			guess_stop_index:0,
            guess_data:{
				time:[],//结算时间
				usdt:[],//竞猜金额
				recharge:0,//手续费
				btc_price:'',//昨天收盘价格
				is_open:0,
				opentime:{
					start_time:'',
					end_time:''
				}
            },
            //下单数据
			order_data:{
				point:'',
				quiz:'',
				usdt:'',
				order_money:''
			},
			showGuessModal:true,//竞猜模态框
			guess_type:1,//1猜涨 2 猜跌
			guessingOrderList:[],
			//实时价格
			btn_price_data:{
				vol:'',//成交量
				high:'',//最高
				low:'',//最低
				sell:'',//卖一价
				last:'',//最新成交价
				buy:''//买一价
			},

        }
    },
    filters:{
		//保留两位小数
		toFixedNum(val,num){
			if(val){
				return parseFloat(val).toFixed(num);
			}else{
				return ''
			}
			return val
		}
	},
	computed:{
		...mapState(['user_info']),//用户信息
	},
    created(){
        if(this.$route.params.id){
			this.isLogin = true;
			this.utils.info('已登录...');
			this.user_info.user_id = this.$route.params.id;

			//获取用户信息
			//this.getUserInfo(1);
		}else{
			this.utils.warn('未登录...');
		}
    },
    mounted(){

    },
    methods:{
        //返回app
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, () => {})
		},
		//去充值
		goRecharge(){
			this.utils.info('去充值...');
			this.$bridge.callhandler('goRecharge', {tag:"去充值"}, () => {})	
		},
		//设置支付密码
		goPay(){
			this.utils.info('设置支付密码...');
			this.$bridge.callhandler('goPay', {tag:"设置支付密码"}, (data) => {})
		},
		//去登录
		goLogin(){
			this.utils.info('去登录...');
			this.$bridge.callhandler('goLogin', {tag:"去登录"}, (data) => {})
		},
		//获取用户信息 type 1 刷新页面数据 2 只刷新用户信息
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_usdt;
                    this.user_info.pay_password = data.pay_password;
					this.user_info.token = data.token;

					if(type == 1){
						//获取页面数据
						this.getPageData();

						//持仓
						this.getGuessingList();
					}
					
					
                }else{
					this.utils.err('获取用户信息...err...3s后再次请求');
					let getUserInfoTimeout = setTimeout(() =>{
						this.getUserInfo(type);
						clearTimeout(getUserInfoTimeout);
					},3000)	
				}
			})
        },
        //查看历史记录
		goGuessList(){
			if(!this.isLogin){
				this.utils.showToast('请先登录...');
				this.goLogin();
				return false;
			}
			this.$router.push({
				name:'guessList'
			})	
		},
		//查看玩法规则
		goGuessRule(){
			this.$router.push({
				name:'guessRule'
			})
        },
        //获取页面数据
		getPageData(){
			let option = {
				type:'1',
				token:this.user_info.token
			}
			this.http.post(this.http._HOST + 'wave/basic_usdt',option).then(result =>{
				this.utils.info('获取页面数据',result);
				if(result.success){
					this.guess_data = result.data;

					//初始化下单数据
					//this.initOrderData();

				}else{
					this.utils.err('获取页面数据失败...3s后再次请求');
					let getPageDataTimeout = setTimeout(() =>{
						this.getPageData();
						clearTimeout(getPageDataTimeout);
					},3000)
				}
			})
        },
        //持仓
		getGuessingList(){
			let option = {
				token:this.user_info.token,
				type:'1'
			}
			this.http.post(this.http._HOST + 'wave/order_list',option).then(result =>{
				this.utils.info('持仓',result);
				if(result.success){
					this.guessingOrderList = result.data;

					// if(this.guessingOrderList.length > 0){
					// 	this.guessingInterval = setInterval(() =>{

					// 	},5000)	
					// }

					if(this.guessingOrderList.length > 0){
						let guessingListTimeOut = setTimeout(() =>{
							this.utils.info('定时刷新持仓...');
							//刷新用户信息
							this.getUserInfo(2);
							//刷新持仓
							this.getGuessingList();
							clearTimeout(guessingListTimeOut);	
						},1000)
					}

				}else{
					this.utils.err('持仓...3s后再次请求');
					let getGuessingListTimeout = setTimeout(() =>{
						this.getGuessingList();
						clearTimeout(getGuessingListTimeout);
					},3000)	
				}
			})
        },
        //显示竞猜模态框
		showGuessModalFun(type){
			this.guess_type = type;
			this.showGuessModal = true;
        },
        //关闭竞猜模态框
		closeGuessModalFun(){
			this.showGuessModal = false;

			//初始化数据
			//this.initOrderData();
        },
        //选择竞猜金额
		changeGuessMoney(item,index){
			
			if(this.guess_money_index == index){
				return false;
			}
			this.guess_money_index = index;
			//this.order_data.usdt = item.usdt_num;
			// this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
		},
    }
}
</script>


<style lang="scss" scoped>
    .guess-page{
		width:100%;
		min-height:100%;
		background: #20232c;
		position: relative;	
		padding:46px 0 50px;
	}

	.van-nav-bar{
		background: #20232c;
	}
	.van-nav-bar__title{
		font-size:18px;
		color:#fff;
	}
	.van-nav-bar .van-icon{
		font-size:20px;
		color:#fff;
	}
	.van-nav-bar__text{
		font-size:12px;
		//color:#7C7D8F;
		color:#fff;
	}

    //底部
	.bottom{
		position: fixed;
		z-index: 19;
		width:100%;
		left:0;
		bottom:0;
		height:50px;
		background: #fff;

		.van-row{
			height:50px;
			line-height: 50px;
			font-size:16px;
			text-align: center;
			color:#353535;
		}
		.guess-up{
			background: #48be7b;
			color:#fff;
			height:100%;
		}
		.guess-down{
			background: #ff4a4a;
			color:#fff;
			height:100%;
		}
		.guess-up-box,.guess-down-box{
			position: relative;
		}
		.guess-down-box-shadow,.guess-up-box-shadow{
			position: absolute;
			width:100%;
			height:100%;
			top:0;
			left:0;
			background: #000;
			opacity: 0.6;
		}
	}

    //模态框
	.modal-box{
		position: fixed;
		z-index:29;
		width:100%;
		height:100%;
		top:0;
		left:0;
		
	}
	.modal-shadow{
		position: absolute;
		z-index:39;
		width:100%;
		height:100%;
		top:0;
		left:0;
		background: #000;
		opacity: 0.6;
	}
	.modal-content{
		position: absolute;
		z-index:99;
		width:100%;
		left:0;
		bottom:0;
		background: #3B3B46;
		min-height:200px;
	}
	.modal-content-title{
		position: relative;
		padding:30px 15px 20px;
		font-size:18px;
		text-align: center;
		color:#fff;
	}
	.modal-content-title-about,
	.modal-content-title-close{
		position: absolute;
		width:16px;
		height:16px;
		top:50%;
		transform: translateY(-50%);
	}
	.modal-content-title-about{
		left:15px;
	}
	.modal-content-title-close{
		right:15px;
	}
	.modal-content-item{
		padding:0 15px 15px;
	}
	.modal-content-item-name{
		font-size: 14px;
		color:#fff;
		padding-bottom: 10px;
	}
	.modal-content-item-box{
		white-space: nowrap;
		overflow: hidden;
	}
	.modal-content-item-content{
		overflow-x: auto;
	}
	.modal-content-item-content::-webkit-scrollbar{
		display: none;
	}
	.modal-content-item-content span{
		display: inline-block;
		width:60px;
		height:30px;
		line-height:30px;
		text-align: center;
		font-size:14px;
		color:#7C7D8F;
		border:1px solid #292930;
		border-radius: 2px;
		margin-right:10px;
	}
	.modal-content-item-content span:last-child{
		margin-right:0;
	}
	.modal-content-item-content span.active{
		// color:#fff;
		// background: #292930;

		border:1px solid #f07d30;
		color:#f07d30;
	}
	.modal-content-user-info{
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding:0 15px 15px;
		font-size:12px;
		color:#78798A;
	}
	.modal-content-user-info-left span{
		display: inline-block;
		font-size:14px;
		color:#f07d30;
		margin-left:30px;
	}
	.modal-content-btn{
		padding:15px;

		.van-row{
			
			// font-size:16px;
			// text-align: center;
			// color:#353535;
			border-radius: 5px;
			overflow: hidden;
		}
		.modal-content-btn-left{
			background: #292930;
			height:44px;
			text-align: center;
			padding-top:4px;
		}
		.modal-content-btn-left-total{
			font-size:14px;
			color:#fff;
		}
		.modal-content-btn-left-tip{
			font-size:12px;
			color:#7C7D8F;
		}
		.modal-content-btn-right{
			height:44px;
			line-height: 44px;
			background: #48be7b;
			color:#fff;
			font-size:18px;
			text-align: center;
		}
		.modal-content-btn-right-down{
			background: #ff4a4a;
			height:100%;
		}
		// .guess-up{
		// 	background: #48be7b;
		// 	color:#fff;
		// }
		// .guess-down{
		// 	background: #ff4a4a;
		// 	color:#fff;
		// }
	}

</style>


